/* Core Styles */

body {
    max-width: 2000px;
    margin: 0 auto;
}

h1, h2, h3 {
    margin: 0;
}

h1 {
    font-size: 3em;
}

h2 {
    font-size: 2.5em;
}

/* App */
.app {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}



/* Input */

.weather-input {
    margin: 2em 0;
}

.app-title {
    font-size: 2em;
}

.input-title {
    font-size: 1.5em;
    text-align: center;
    margin: 1em 0 1em 0;
}

form {
    text-align: center;
    margin: 1em 0 1em 0;
}

input {
    margin-bottom: 2em;
    text-transform: uppercase;
    max-width: 250px;
    outline: none;
    position: relative;
    right: 10px;
}


/*Weather Display */

.loader-wrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    margin-top: 8em;
}

.current-weather {
    background: #f3f3f3;
    color: white;
    min-height: 25em;
}

.empty-weather {
    color: rgb(33, 33, 33);
    text-align: center;
}

.empty-weather p {
    font-size: 1em;
}

.no-city {
    margin-top: 2em;
    text-align: center;
}

.located-city-display {
    background-size: cover!important;
    background-position: center!important;
    background-repeat: no-repeat!important;
    min-height: 20em;
}

.conditions-title {
    margin: 1em 0;
}

.time-button-wrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    padding-top: 1em;
}

.lrg-screen-time {
    display: none;
}

.cels-fahr {
    width: 186px;
    border: 2px solid #959595;
}

.city-wrapper {
    text-align: center;
    margin: 1em 0;
}
.city-name, .country-name {
    color: white;
}

.temp-wrapper {
    margin: 1em 0;
}

.sml-screen-time {
    text-align: center;
    margin: 1em 0;
}

.time {
    font-size: 2.5em;
}

.conditions-title h4 {
    font-size: 2em;
}

.conditions-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.condition-wrapper {
    color: #252525;
    width: 280px;
    margin: 1em auto;
    border-radius: 5px;
}

.weather-type-icon {
    height: 70px;
}

.icon-wrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.conditions-icon {
    position: relative;
    right: 10px;
}

.description-wrapper {
    text-align: center;
}

.description {
    position: relative;
    right: 10px;
}

/*Tagline */
.tagline-wrapper {
    text-align: center;
    margin-top: 1em;
    padding-bottom: 2em;
}

.tagline {
    font-size: 2em;
}



/* MEDIA QUERIES */
@media screen and (min-width: 700px) {
    /*Core Styles*/
    .app {
        -ms-flex-direction: row;
            flex-direction: row;
    }

    h2 {
        font-size: 3em;
    }

    /*Input*/
    .weather-input {
        width: 40%;
        margin-top: 10em;
    }

    /*Weather Display*/
    .current-weather {
        width: 60%;
        height: 55em;
    }

    .empty-weather p {
        font-size: 2em;
    }

    .located-city-display {
        min-height: 55em;
    }

    .no-city {
        font-size: 3em;
        margin-top: 4em;
    }

    .time-button-wrapper {
        -ms-flex-pack: justify;
            justify-content: space-between;
        padding: 2em 1em 0 1em;
    }

    .lrg-screen-time {
        display: block;
    }

    .sml-screen-time {
        display: none;
    }

    .city-wrapper {
        margin-top: 4em;
    }

    .city-name, .country-name {
        display: inline-block;
    }

    .country-name {
        padding-left: 10px;
    }

    .conditions-container {
        -ms-flex-direction: row;
            flex-direction: row;
        -ms-flex-pack: center;
            justify-content: center;
    }

    .conditions-wrapper {
        margin-top: 3em;
    }

    .tagline-wrapper {
        margin-top: 6em;
    }
}

@media screen and (min-width: 900px) {
    .condition-wrapper {
        margin: 1em;
    }
}

@media screen and (min-width: 1200px) {
    /* Input */
    .app-title {
        font-size: 3em;
    }


    /* Weather Display */
    .located-city-display {
        min-height: 60em;
    }

    .current-weather {
        height: 60em;
    }
}

@media screen and (min-width: 1500px) {
    h1 {
        font-size: 4em;
    }

    h2 {
        font-size: 3em;
    }

    .conditions-wrapper {
        margin-top: 6em;
    }

    .conditions-title h4 {
        font-size: 3em;
    }
    
    .temp {
        font-size: 4em;
    }

    .tagline {
        font-size: 3em;
    }
}

@media screen and (min-width: 2000px) {
    .located-city-display {
        min-height: 70em;
    }

    .current-weather {
        height: 70em;
    }
}


